.btn_standart {
  transition: all .2s;
  min-width: 170px;
  display: block;
  cursor: pointer;
  width: -moz-fit-content;
  width: fit-content;
  letter-spacing: normal;
  border-radius: 4px;
  text-align: center;
  display: flex;
  justify-content: center;
  border: none;
  outline: none;
  padding: 14.5px 10px; }
  .btn_standart_orange {
    color: #fff;
    background: #F28820;
    border: solid 1px transparent !important; }
    .btn_standart_orange:hover {
      border: solid 1px #cd731a !important;
      background: #cd731a; }

@media (max-width: 435px) {
  .btn_standart {
    font-size: 15px;
    padding: 10px 10px; } }
