@charset "UTF-8";
.header {
  position: fixed;
  background: #fff;
  z-index: 999;
  width: 100%;
  border-bottom: 1px solid #CCCCCC;
  overflow: hidden; }
  .header_info__block {
    position: relative;
    width: 100%;
    height: 44px;
    background: #020202; }
    .header_info__block .container {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between; }
  .header_info_time {
    margin-left: 5px; }
    .header_info_time__block {
      display: flex;
      cursor: default;
      align-items: center; }
  .header_info_address {
    transition: all .2s; }
    .header_info_address:hover {
      color: #F28820; }
    .header_info_address__block {
      position: absolute;
      top: 13px;
      left: 50%;
      transform: translateX(-50%); }
  .header_info_contacts__block {
    display: flex;
    align-items: center; }
  .header_info_contacts_phone {
    transition: all .2s;
    margin-right: 12px; }
    .header_info_contacts_phone:hover {
      color: #F28820; }
  .header_info_contacts_link {
    display: flex;
    margin: 0px 6px; }
    .header_info_contacts_link svg path {
      transition: all .2s; }
    .header_info_contacts_link:hover path {
      fill: #F28820; }
  .header_content__block {
    position: relative;
    width: 100%;
    height: 90px;
    background: #fff; }
    .header_content__block .container {
      position: relative;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between; }
  .header_content_label {
    background: #F2F2F2;
    border: 1px solid #ACACAC;
    border-radius: 31px;
    padding: 7px 15px;
    width: 350px;
    display: flex;
    align-items: center;
    cursor: pointer; }
    .header_content_label img {
      margin-right: 12px; }
    .header_content_label input:-webkit-autofill {
      -webkit-box-shadow: inset 0 0 0 50px #F2F2F2 !important;
      /* Цвет фона */
      -webkit-text-fill-color: #000 !important;
      /* цвет текста */
      color: #000 !important;
      /* цвет текста */ }
  .header_content_svg {
    cursor: pointer; }
    .header_content_svg path {
      transition: all .2s; }
    .header_content_svg:hover path {
      fill: #F28820; }
  .header_content_user:hover path {
    stroke: #F28820; }
  .header_content_user_img {
    display: flex;
    width: 36px;
    height: 36px;
    overflow: hidden;
    border-radius: 50%; }
    .header_content_user_img img {
      transition: all .3s;
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: grayscale(100%); }
    .header_content_user_img:hover img {
      filter: grayscale(0%); }
  .header_content_shop {
    position: relative;
    margin-left: 27px;
    margin-right: 37px; }
    .header_content_shop_counter {
      position: absolute;
      width: 18px;
      height: 18px;
      top: -10px;
      right: -10px;
      background: #E52D23;
      font-style: normal;
      font-weight: bold;
      color: #fff;
      border-radius: 50%;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      font-size: 10px;
      line-height: 1px; }
  .header_content_right {
    display: flex;
    align-items: center; }
  .header_content_left {
    display: flex;
    align-items: center; }
  .header_content_nav {
    transition: all .2s;
    margin: 0px 16px; }
    .header_content_nav:hover {
      color: #F28820; }
  .header_content_logo {
    margin-right: 20px; }

.main_search {
  outline: none;
  background: transparent;
  cursor: pointer;
  border: none;
  width: calc(100% - 20px); }

.anim_left {
  animation-duration: 1s;
  animation-name: dinamic_left;
  animation-fill-mode: forwards; }

@keyframes dinamic_left {
  0% {
    left: -100px;
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    left: 0;
    opacity: 1; } }

.anim_right {
  animation-duration: 1s;
  animation-name: dinamic_right;
  animation-fill-mode: forwards; }

@keyframes dinamic_right {
  0% {
    right: -100px;
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    right: 0;
    opacity: 1; } }

.anim_top {
  animation-duration: 1s;
  animation-name: dinamic_top;
  animation-fill-mode: forwards; }

@keyframes dinamic_top {
  0% {
    top: -50px;
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    top: 0;
    opacity: 1; } }

.anim_bottom {
  animation-duration: 1s;
  animation-name: dinamic_bottom;
  animation-fill-mode: forwards; }

@keyframes dinamic_bottom {
  0% {
    bottom: -50px;
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    bottom: 0;
    opacity: 1; } }
