main {
  position: relative; }

.unselectable {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                    not supported by any browser */ }

.padding_navbar {
  padding-top: 165px; }

.container {
  padding: 0px 5%;
  width: 100%; }

.upper_text {
  text-transform: uppercase; }

.absolute_center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.sub_title {
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 400; }
  .sub_title_0 {
    font-size: 12px; }
  .sub_title_1 {
    font-size: 14px; }
  .sub_title_2 {
    font-size: 16px; }

.midle_title {
  font-weight: 500;
  font-stretch: normal;
  font-style: normal; }
  .midle_title_0 {
    font-size: 12px; }
  .midle_title_5 {
    font-size: 20px; }

.standart_title {
  font-weight: 600;
  font-stretch: normal;
  font-style: normal; }
  .standart_title_0 {
    font-size: 12px; }
  .standart_title_1 {
    font-size: 14px; }
  .standart_title_3 {
    font-size: 18px; }
  .standart_title_5 {
    font-size: 24px; }

.bold_title {
  font-weight: 700;
  font-stretch: normal;
  font-style: normal; }
  .bold_title_0 {
    font-size: 12px; }
  .bold_title_10 {
    font-size: 56px; }

.bigger_title {
  font-weight: 900;
  font-stretch: normal;
  font-style: normal; }
  .bigger_title_0 {
    font-size: 12px; }
  .bigger_title_4 {
    font-size: 20px; }
  .bigger_title_6 {
    font-size: 28px; }

.color_red {
  color: #E52D23; }

.color_blue {
  color: #103989; }

.color_black {
  color: #000; }

.color_white {
  color: #fff; }

.color_gray {
  color: #ACACAC; }
  .color_gray_0 {
    color: #7D7D7D; }
  .color_gray_1 {
    color: #F2F2F2; }
  .color_gray_2 {
    color: #19191D; }

.color_orange {
  color: #F28820; }

@media (max-width: 1560px) {
  .preferens_number__block .bigger_title_8 {
    font-size: 60px; } }

@media (max-width: 1320px) {
  .bigger_title_3 {
    font-size: 31px; } }

@media (max-width: 992px) {
  .bigger_title_3 {
    font-size: 24px; } }

@media (max-width: 800px) {
  .sub_title_10 {
    font-size: 58px; } }

@media (max-width: 490px) {
  .standart_title_4 {
    font-size: 21px; }
  .standart_title_5 {
    font-size: 18px; }
  .standart_title_3 {
    font-size: 17px; }
  .bigger_title_8 {
    font-size: 30px !important; }
  .standart_title_7 {
    font-size: 40px; } }

@media (max-width: 435px) {
  .bigger_title_1 {
    font-size: 18px; }
  .standart_title_2_1 {
    font-size: 18px; } }
