.footer {
  position: relative;
  overflow: hidden;
  background: #2E2E2E; }
  .footer_content_logo {
    margin-right: 20px; }
  .footer_content_nav {
    transition: all .2s;
    margin: 0px 16px; }
    .footer_content_nav:hover {
      color: #F28820; }
  .footer__block {
    padding: 27px 0px;
    background: #2E2E2E; }
    .footer__block .container {
      display: flex;
      align-items: center; }
  .footer_copyright {
    width: 100%;
    text-align: center;
    background: #242424;
    padding: 10px 0; }
